.loader_container{
  width: 100%;
  height: 900px;
  display: flex;
  justify-content: center;
}
.loader{
  position: absolute;
  top: 40%;
  width: 64px;
  height: 64px;
  border: 6px solid  var(--primary);
  border-bottom-color: transparent;
  border-radius:50% ;
  animation: loader 1s infinite linear; 
}
@keyframes loader {
  from {
    transform: rotate(0deg);
  }
  to{
    transform: rotate(360deg);
  }
}
@media all and (max-width:767px){
  .loader_container{
    height: 600px;
  }
  .loader{
    width: 32px;
    height: 32px;
    
  }
}