.body_content{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background-color: var(--DD_bg_color);
    display: block;
    border-radius:0 0  5px 5px;
    padding-top: 30px;
    padding-bottom: 30px;
}
.hidden{
    display: none;
}
.p_content{
    width: auto;
    height: auto;
    padding: 0 20px 0 20px;
    font-size: 18px;
    font-weight: 400;
    color: var(--primary);
    line-height: 26px;
    margin: 0;
    display: flex;
    flex-direction: column;
}
.p_content_about{
    width: auto;
    height: auto;
    padding: 0 20px 0 20px;
    font-size: 24px;
    font-weight: 400;
    color: var(--primary);
    line-height: 34px;
    margin: 0;
    display: flex;
    flex-direction: column;
}
.list_content{
    margin: 0;
    padding: 0;
}
@media all and (max-width:767px){
    .p_content,
    .p_content_about{
        font-size: 12px;
        line-height: 17px;
        padding: 0 10px 0 10px;
    }
    .p_content_about{
        min-height: 109px; 
        /* 143px-17pad-17pad = 109px */
    }
        
    .body_content{
        padding-top: 17px;
        padding-bottom: 17px;
    }
}